.btn-default {
  border: 1px solid rgba(0, 0, 0, 0.342);
  border-radius: 0;
}
.active {
  background-color: cornflowerblue;
  color: #fff;
}
.active:hover {
  color: #fff;
}
.btn-group{ 
  width: 25%;
  margin-bottom: 4px;
  border-radius: 0 0 0 25px;
}
.btn:last-child {
  border-radius: 5px;
}

